button, html input[type="button"], input[type="reset"], input[type="submit"] {
	-webkit-appearance: button;
	cursor: pointer;
}
input[type="search"] {
	-webkit-appearance: textfield;
	box-sizing: content-box;
}
input[type="search"]::-webkit-search-cancel-button, input[type="search"]::-webkit-search-decoration {
	-webkit-appearance: none;
}
.top-nav {
	background: $white;
	background: -moz-linear-gradient(left, $white 0%, $white 72%, $gray 72%, $gray 100%);
	background: -webkit-linear-gradient(left, $white 0%, $white 72%, $gray 72%, $gray 100%);
	background: linear-gradient(to right, $white 0%, $white 72%, $gray 72%, $gray 100%);
	filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='$white', endColorstr='$gray', GradientType=1);
	min-height: 36px;

	@include breakpoint(x-large) {
		background: -moz-linear-gradient(left, $white 0%, $white 65%, $gray 65%, $gray 100%);
		background: -webkit-linear-gradient(left, $white 0%, $white 65%, $gray 65%, $gray 100%);
		background: linear-gradient(to right, $white 0%, $white 65%, $gray 65%, $gray 100%);
		filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='$white', endColorstr='$gray', GradientType=1);
	}
	.container{
		@include breakpoint(large){
			width:1140px;
		}
	}
	ul {
		margin: 0;
		list-style: none;
		float: right;
		position: relative;

		&:before {
			-webkit-transform: skew(20deg);
			-moz-transform: skew(20deg);
			-o-transform: skew(20deg);
			-ms-transform: skew(20deg);
			transform: skew(20deg);
			height: 36px;
			width: 28px;
			background: $gray;
			display: inline-block;
			content: "";
			position: absolute;
			left: -6px;
		}
		li {
			float: left;
			font: 700 12px/36px $primary-font;
			letter-spacing: .04em;
			color: $white;
			position: relative;
			padding: 0 14px;
			background: $gray;

			i {
				color: $light-gray-1;
				margin-right: 10px;
			}
			a {
				display: inline-block;
				color: $white;
				font: 300 12px/36px $primary-font;

				&:hover {
					color: $theme-color;
				}
			}
			&:after {
				width: 1px;
				position: absolute;
				top: 0;
				bottom: 0;
				right: 0;
				background: $border-color-1;
				display: inline-block;
				content: "";
			}
			&:first-child {
				a {
					font: 700 12px/36px $primary-font;
					color: $white;
				}
			}
		}
		li.get-quote{
			display: none;
			padding:0;
			a{
				color:$white;
				padding:0 14px;
				@include transition(0.5s);
				background: $theme-color;
				&:hover{
				background:$theme-color-alt;
			}
			}
			@include breakpoint(xx-small){
					display: block;
				}
			
		}
		&:after {
			clear: both;
			display: block;
			content: "";
		}
	}
}
.three-col {
	h3 {
		font-size: 18px;
		font-family: $secondary-font;
		margin-bottom: 20px;
	}
	p {
		margin-bottom: 10px;
	}
	figure {
		width: 90px;
		height: 90px;
		margin: 0 auto 35px;
		background: $white;
		text-align: center;
		line-height: 90px;

		img {
			vertical-align: middle;
		}
	}
}
.home .one-col {
	text-align: center;

	@include breakpoint(small) {
		max-width: 550px;
		margin: 0 auto;
	}
	h2 {
		font: $bold 28px/1 $primary-font;
		color: $gray;
		letter-spacing: -.03em;

		@include breakpoint(small) {
			font-size: 52px;
			margin-bottom: 56px;
		}
	}
	p {
		color: $white;
		font-size: 16px;
		line-height: 26px;
	}
}
.gform_footer{
	img{
		display: none;
	}
}
.gform_heading {
	.gform_title {
		font: $bold 28px/1 $primary-font;
		color: $white;
		letter-spacing: -.03em;

		@include breakpoint(small) {
			font-size: 52px;
			margin-bottom: 56px;
		}
	}
	.gform_description {
		color: $white;
		font-size: 20px;
		line-height: 26px;
		display: block;
		margin-bottom: 57px;
	}
}
.testimonial {
	padding: 0;

	.container {
		position: static;
		top: inherit;
		left: inherit;
		transform: translate(0, 0);
		-webkit-transform: translateY(0, 0);
	}
	.testi {
		font: $regular 26px/42px $primary-font;
		color: $gray;
	}
	.testimonial__content__meta {
		margin-top: 40px;
		letter-spacing: .2em;
		font: $bold 11px $primary-font;
		color: $gray;
		text-transform: uppercase;

		span {
			font: $regular 14px $base-font;
			text-transform: initial;
			letter-spacing: normal;
			color: $base-color;
			margin-top: 5px;
		}
	}
	.owl-dots {
		display: block;

		@include breakpoint(small) {
			display: none;
		}
	}
}
.owl-theme {
	.owl-nav [class*='owl-'] {
		background: none;
		color: #acacac;
		font-size: 32px;
		margin: 0;
		padding: 0;
		line-height: 1;

		&:hover {
			background: none;
			color: $theme-color;
		}
	}
	.owl-nav {
		margin: 0;
		position: relative;
		top: 0;

		.owl-prev, .owl-next {
			position: absolute;
			top: -128px;
			display: none;

			@include breakpoint(small) {
				display: block;
			}
		}
		.owl-prev {
			left: 0;

			@include breakpoint(small) {
				left: -165px;
			}
		}
		.owl-next {
			right: 0;

			@include breakpoint(small) {
				right: -165px;
			}
		}
	}
}
.background-overlay {
	h3 {
		color: $gray;
	}
	p {
		color: $white;
	}
	.button--border {
		border-color: $white;

		&:hover {
			border-color: $theme-color;
		}
	}
}
.overlay {
	position: relative;

	&:before {
		position: absolute;
		left: 0;
		right: 0;
		bottom: 0;
		top: 0;
		content: "";
		background: rgba(43, 44, 45, .75);
		display: block;
	}
	h2, p {
		color: $white;
		margin-bottom: 35px;
	}
	p {
		font-size: 20px;
		margin-bottom: 57px;
	}
}
