.global-form {
	form {
		margin-bottom: 0;

		ul {
			list-style: none;
			margin: -15px 0 0 0;

			li {
				margin-top: 15px;

				label {
					display: none;
				}
				input[type='text'], input[type='password'], input[type='email'], input[type='password'], textarea, select {
				    -webkit-appearance: none;
				    -moz-appearance: none;
				    appearance: none;
					@include border-radius(4px);
					padding: 13px 15px;
					background: $white;
					width: 100%;
					outline: none;
					@include border(all, 1px solid rgba($black, .2));
					font: $regular 14px $primary-font;
					box-shadow: none;
				}
				textarea {
					resize: none;
					height: 125px;
					overflow: hidden;
				}
			}
		}
	}
}
.overlay {
	.global-form {
		.gform_body{
		max-width: 520px;
		margin: 0 auto;
		}

		input[type='text'], input[type='password'], input[type='email'], input[type='password'], textarea, select {
			background: rgba($white, .15);
			color: $white;
			@include border(all, none);
			// -webkit-appearance: none;
		}
		select {
			option {
				background: rgba($black, .6);
			}
		}
		.ginput_container_select:after {
			color: $white;
		}
		::-webkit-input-placeholder {
			color: $white;
		}
		::-moz-placeholder {
			color: $white;
		}
		:-ms-input-placeholder {
			color: $white;
		}
		:-moz-placeholder {
			color: $white;
		}
	}
}
.global-form {
	.gform_footer {
		margin-top: 25px;
	}
}
.ginput_container_select, .global-form li.select {
	position: relative;
}
select::-ms-expand {
	display: none;
}
.ginput_container_select:after, .global-form li.select:after {
	content: "\f107";
	font: 14px $icon-font;
	color: $dark-gray;
	right: 11px;
	top: 18px;
	padding: 0 0 2px;
	position: absolute;
	pointer-events: none;
}
.ginput_container_select select, .global-form li.select select {
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
	display: block;
	-ms-word-break: normal;
	word-break: normal;
}
.gform_footer {
	text-align: center;

	.gform_button.button {
		background: $theme-color-alt;
		min-width: 195px;

		&:hover {
			background: $theme-color;
		}
	}
}
.half-width, .overlay .contact-form .half-width {
	@include breakpoint(small) {
		width: 252px;
		display: inline-block;
		margin-right: 15px;
		vertical-align: top;
	}
	&:after {
		clear: both;
		display: block;
		content: "";
	}
}
