.slider, .hero-banner {
	position: relative;

	&:after {
		background: url(../images/img-slanted.png) no-repeat left bottom/100%;
		position: absolute;
		bottom: 0;
		content: "";
		height: 75px;
		width: 100%;

		@include breakpoint(xx-small) {
			height: 125px;
		}
		@include breakpoint(x-small) {
			height: 150px;
		}
		@include breakpoint(small) {
			height: 177px;
		}
		@include breakpoint(large) {
			background: url(../images/img-slanted-large.png) no-repeat left bottom/100%;
		}
		@include breakpoint(x-large) {
			height: 40%;
		}
	}
}
.hero-banner .container {
	text-align: center;
}
svg {
	overflow: hidden;
}
.video-banner {
	height: 100vh;
	position: relative;

	@include breakpoint(small) {
		height: 75vh;
	}
}
.video-wrap {
	position: relative;
	width: 100%;
	height: 100%;
	overflow: hidden;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;

	.poster {
		display: block;
		height: 100vh;

		@include breakpoint(small) {
			height: 75vh;
		}
	}
	video, iframe {
		display: none;
		position: absolute;
		z-index: 0;
		bottom: 0;
		min-width: 100%;
		height: 125vh;

		@include breakpoint(large) {
			display: block;
		}
	}
	.slider__caption {
		position: absolute;
		width: 100%;
		top: 15vh;
		height: auto;

		@include breakpoint(medium) {
			height: 100%;
		}
		@include breakpoint(x-large) {
			top: 25vh;
		}
	}
}
.video-content {
	position: absolute;
	width: 100%;
	top: 0;

	.container {
		width: 100%;
		padding: 0;

		@include breakpoint(small) {
			max-width: 700px;
		}
	}
}
.video-wrap {
	height: 100%;
}
.upface {
	margin-top: -25px;
	margin-bottom: -7px;

	@include breakpoint(medium) {
		margin-top: 0;
	}
}
.downface {
	margin-bottom: -31px;

	@include breakpoint(small) {
		margin-bottom: -7px;
	}
}
.slider:after {
	z-index: 1
}