.three-col-list{
	ul{
		list-style: none;
		margin-top:-60px;
		margin-bottom: 0;
		&:after{
			clear:both;
			display: block;
			content:"";
		}
		@include breakpoint(x-small){
			display: flex;
			flex-wrap:wrap;
			margin-left: -15px;
			width: 100%;
		}
		@include breakpoint(small){
			
			
		}
		li{
			margin-top:60px;
			@include breakpoint(x-small){
				float: left;
				margin-left: 30px;
				width: calc(50% - 30px);
			}
			@include breakpoint(small){
				width: calc(33.33% - 30px);
			}
			.download-img{
				width: 100%;
				height: 200px;
			}
			h2{
				font-size: 20px;
				font-weight: $regular;
				margin: 15px 0;
				min-height: 40px;
			}
		}
	}
	form li{
		margin:0;
	}
}
.removalist.three-col-list{
	.section-title{
		margin-bottom: 90px;
		@include breakpoint(small){
			margin-bottom: 0;
		}
		&__content{
			max-width: 100%;
		}
	}
	ul{
		li{
			h2{
				min-height: 65px;
				margin-bottom: 0;
			}
			.tel{
				span{
					display: block;
				}
				@include breakpoint(small){
					min-height: 48px;
				}
			}
			.url{
				min-height: 24px;
				margin: 15px 0;
				word-break:break-all;
				a{
					display: inline-block;
					color:$theme-color;
					&:hover{
						color:$theme-color-alt;
					}
				}
			}
			.description{
				p{
					font-size: 15px;
					line-height: 24px;
					// &:before{
					// 	// content:"Description : ";
					// 	display: inline-block;
					// 	margin-right:3px;
					// }
				}
			}
		}
	}
}