.space-estimator {
	.one-col {
		p {
			margin-bottom: 20px;

			&:last-child {
				margin-bottom: 0;
			}

		}

	}

	.resp-vtabs {
		a {
			position: relative;
			display: block;

			span {
				display: block;
				font-size: 12px;
			}

			i {
				position: absolute;
				display: none;
				right: 15px;
				top: 21%;
				font-size: 24px;

				@include breakpoint(small) {
					display: block;
				}

			}

		}

	}

	.space-estimator-wrapper {
		.content {
			margin-bottom: 30px;

			@include breakpoint(small) {
				width: 55%;
				float: left;
				margin-bottom: 0;
			}

		}

		.img-section {
			@include breakpoint(small) {
				width: 40%;
				float: right;
			}

			em {
				text-align: center;
				font-size: 13px;
				display: block;
			}

		}

		&:after {
			clear: both;
			display: block;
			content: "";
		}

	}

}

.space-estimator .scrollable-div.resp-vtabs a i {
	display: none;

	@include breakpoint(medium) {
		display: block;
	}

}

.space-estimator {
	.scrollable-div {
		.scroll-content {
			@include breakpoint(medium) {
				width: 30%;
			}

		}
.resp-tabs-container {

		@include breakpoint(large) {
			width: 70%;
		}

	}

	}

}

.scrollable-div {
	.scroll-content {
		@include breakpoint(small) {
			background: $light-gray;
			padding: 15px 0;
			border: solid #c1c1c1;
			border-right: 1px solid #FFF;
			float: left;
			display: none;
			border: 1px solid #c1c1c1;
			border-right: 1px solid #FFF;
			margin-right: -1px;
		}

		@include breakpoint(medium) {
			width: 25%;
			display: block;
			height: 600px;
			overflow: hidden;
		}

		ul.resp-tabs-list {
			width: 100%;
			border: none;
			margin: 0;

			li {
				border-bottom: 1px solid #c1c1c1;
			}

		}

	}

	.mCSB_inside>.mCSB_container {
		margin: 0;
	}

	.resp-tabs-container {
		@include breakpoint(medium) {
			float: right;
			height: 600px;
		}

		@include breakpoint(large) {
			width: 75%;
		}

	}

}

.mCSB_scrollTools {
	z-index: 1;
	width: 4px;
}

.mCSB_scrollTools .mCSB_dragger.mCSB_dragger_onDrag .mCSB_dragger_bar, .mCSB_scrollTools .mCSB_dragger:active .mCSB_dragger_bar {
	background: $dark-gray;

	&:hover {
		background: $dark-gray;
	}

}

.mCSB_scrollTools .mCSB_dragger .mCSB_dragger_bar, .mCSB_scrollTools .mCSB_dragger .mCSB_dragger_bar {
	background: $dark-gray;
	width: 5px;

	&:hover {
		background: rgba(0, 0, 0, 1);
	}

}

.mCSB_scrollTools .mCSB_draggerRail {
	background-color: rgba(0, 0, 0, .5);
}
