.header {
	@include border(top, 1px solid rgba($border-color-1, .1));
	&:after{
		clear:both;
		display: block;
		content:'';
	}
	background: $white;
	position: relative;
	
	
	@include transition(0.5s);
	//border-bottom: 1px solid #F5F5F5;

	&__wrap {
		position: relative;
		-webkit-backface-visibility: hidden;
		@include transition(0.5s);
		background: $white;
		padding: 15px 0;
		@include breakpoint(small){
			padding:26px 0;
		}

		.container {
			// width: 100%;
			padding: 0 15px;

			@include breakpoint(small) {
				padding: 0 15px;
			}
			@include breakpoint(large) {
				width:1140px;
			}
		}
		.logo {
			border-right: none;
			margin: 0 auto;
			float: none;
			display: inline-block;
			padding-right: 0;
			// text-align: center;
			a{
				display: block;
				line-height: .5;
			}
			@include breakpoint(medium) {
				// border-right: 1px solid $light-gray-1;
				// margin-right: 30px;
				// padding-right: 30px;
				float: left;
			}
			img {
				@include transition(0.5s);
				height: 40px;
				@include breakpoint(large) {
					height: 68px;
				}
			}
		}
	}
}
//Search	
#top-search {
	float: right;
	margin-top: -45px;
	@include transition(0.5s);

	@include breakpoint(small) {
		margin: 45px 0 40px 15px;
	}
	form {
		height: 100px;
		left: 0;
		margin: 0;
		opacity: 0;
		padding: 0 15px;
		position: absolute;
		top: 0;
		transition: opacity 0.3s ease-in-out 0s;
		width: 100%;
		background: $white;
		z-index: -2;

		input {
			background-color: transparent;
			border: 0 none;
			border-radius: 0;
			box-shadow: none;
			color: $base-color;
			font-family: "Raleway", sans-serif;
			font-size: 32px;
			font-weight: 700;
			height: 100%;
			letter-spacing: 2px;
			margin-top: 0;
			outline: 0 none;
			padding: 10px 80px 10px 40px;
			width:100%;

			@include breakpoint(small) {
				padding: 10px 0 10px 0;
				font-size: 24px;
			}
		}
	}
	a {
		color: $base-color;
		display: block;
		font-size: 16px;
		height: 14px;
		line-height: 20px;
		position: relative;
		text-align: center;
		transition: color 0.3s ease-in-out 0s;
		width: 14px;
		z-index: 11;

		i {
			left: 0;
			position: absolute;
			top: 0;
			transition: opacity 0.3s ease 0s;
		}
	}
}
body.top-search-open #top-search form {
	opacity: 1;
	z-index: 10;
}
body.top-search-open #top-search a i.fa-search, #top-search a i.fa-times {
	opacity: 0;
}
body.top-search-open #top-search a i.fa-times {
	font-size: 16px;
	opacity: 1;
	z-index: 11;
}
//Cart
.top-cart {
	@include transition(0.5s);

	a {
		position: relative;

		span {
			border-radius: 50%;
			color: #fff;
			display: block;
			font-size: 10px;
			height: 16px;
			left: auto;
			line-height: 16px;
			position: absolute;
			right: -14px;
			text-align: center;
			text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.2);
			top: -7px;
			width: 16px;
			background: $theme-color;
		}
	}
	&__content {
		background-color: #fff;
		border: 1px solid $theme-color;
		border-top: 2px solid $theme-color;
		box-shadow: 0 20px 50px 10px rgba(0, 0, 0, 0.05);
		left: auto;
		opacity: 0;
		position: absolute;
		right: 30px;
		top: 100px;
		transition: opacity 0.5s ease 0s, top 0.4s ease 0s;
		width: 280px;
		z-index: -2;

		&__title {
			border-bottom: 1px solid #eee;
			padding: 12px 15px;

			h4 {
				font-size: 15px;
				font-weight: bold;
				letter-spacing: 1px;
				margin-bottom: 0;
				text-transform: uppercase;
			}
		}
		&__items {
			padding: 15px;

			&__item {
				border-top: 1px solid #f5f5f5;
				margin-top: 15px;
				padding-top: 15px;

				&:first-child {
					border-top: 0 none;
					margin-top: 0;
					padding-top: 0;
				}
				&__image {
					border: 2px solid #eee;
					float: left;
					height: 48px;
					margin-right: 15px;
					transition: border-color 0.2s linear 0s;
					width: 48px;

					a, img {
						display: block;
						height: 44px;
						width: 44px;
					}
				}
				&__desc {
					overflow: hidden;
					position: relative;

					a {
						font-size: 13px;
						font-weight: 600;
						height: auto;
						text-align: left;
						width: auto;
					}
				}
				&__price {
					color: #999;
					display: block;
					font-size: 12px;
					line-height: 20px;
				}
				&__quantity {
					color: #444;
					display: block;
					font-size: 12px;
					position: absolute;
					right: 0;
					top: 2px;
				}
			}
		}
		&__action {
			border-top: 1px solid #eee;
			padding: 15px;
		}
		&__checkout-price {
			font-size: 20px;
			color: $theme-color;
			margin-bottom: 15px;
		}
	}
}
.top-cart.top-cart__open .top-cart__content {
	margin-top: 0;
	opacity: 1;
	z-index: 11;
}

svg{
	height: 100px;
	@include breakpoint(small){
		height: 183px;
	}
}

