.shop-list{
	ul{
		list-style: none;
		margin-top: -30px;
		margin-left: -30px;
		li{
			margin-left: 30px;
			@include breakpoint(small){
				float: left;
				width: calc(50% - 30px;);
			}
			@include breakpoint(small){
				width: calc(33.33% - 30px;);
			}
			ul{
				margin:0;
				li{
					margin:0;
					width: 50%;
				}
			}

		}
		&:after{
			clear:both;
			display: block;
			content:"";
		}
	}
}
.product {
	margin-bottom: 30px;

	&__image {
		overflow: hidden;
		position: relative;
		width: 100%;
		margin-bottom: 15px;

		a {
			display: block;
			width: 100%;
		}
		.sale {
			position: absolute;
			top: 10px;
			left: 10px;
			padding: 0 10px;
		}
	}
	&__overlay {
		bottom: -40px;
		height: 40px;
		left: 0;
		line-height: 40px;
		position: absolute;
		top: auto;
		width: 100%;
		z-index: 6;
		@include transition(0.5s);

		li {
			float: left;
			width: 50%;

			a {
				display: block;
				font-size: 13px;
				letter-spacing: 0;
				margin: 0;
				padding: 0;
				text-align: center;
				@include border-radius(0);
			}
		}
	}
	&__title {
		margin-bottom: 10px;

		h3 {
			font-size: 20px;
			margin: 0;
		}
	}
	&__price {
		font-size: 18px;
		font-weight: $bold;

		del {
			color: #444;
			font-weight: $regular;
			font-size: 16px;
			margin-right: 8px;
		}
	}
	&:hover {
		.product {
			&__overlay {
				bottom: 7px;
			}
		}
	}
}