.section-title{
	margin-bottom: 30px;
	&__content{
		max-width: 550px;
		margin: 0 auto;
	}
	&--bordered, &--bordered-center, &--bordered-right{
		margin-bottom: 30px;
		&:after{
			content:"";
			display: block;
			width: 40px;
			height: 2px;
			background: #444;
		}
	}
	&--bordered{
		&:after{
			margin:30px 0;
		}
		&-center{
			&:after{
				margin:30px auto;
			}
		}
		&-right{
			&:after{
				float: right;
				margin:30px 0;
			}
		}
	}
}

.title{
	margin-bottom: 30px;
		h1,h2,h3,h4,h5,h6{
			margin-bottom: 0;
		}
		&--bordered{
			margin-bottom: 30px;
			h1,h2,h3,h4,h5,h6{
				@include border(bottom, 2px solid $theme-color);	
				padding-bottom: 10px;
			}
		}
		&--muted-text{
			h1,h2,h3,h4,h5,h6{
				margin-bottom: 0;
			}
			span{
				display: block;
				color: #999;
				font-size: 16px;
			}
		}
}
.content--center{
	text-align: center;
}
.content--left{
	text-align: left;
}
.content--right{
	text-align: right;
}
.text--uppercase{
	text-transform: uppercase;
}

.content{
	.button{
		margin-top:  27px;
	}
}