footer {
	.container{
		&:after{
			clear: both;
		content: "";
		display: block;
		}
	}
	.two-col 
	{
		
		@include breakpoint(small){
			margin-left: -30px;
		}
		.col{
		@include breakpoint(small){
			margin-left:30px;
		}
	}
}
	background: $dark-gray;
	&:after {
		clear: both;
		content: "";
		display: block;
	}
	h1, h2, h3, h4, h5, h6 {
		color: $white;
		margin-bottom: 15px;
		text-transform: uppercase;
		letter-spacing: .1em;
		font:$bold 13px/1 $primary-font;
	}
	p, a, li {
		color: $white;
		font-size: 13px;
	}
	a{
		color:rgba($white,.6);
	}
	a:hover {
		color: $theme-color;
	}
	ul {
		list-style: none;
	}
	.main-footer {
		padding:30px 0;
		@include breakpoint(small){
			padding: 55px 0;
		}
		@include breakpoint(medium){
			padding: 55px 0;
		}
		&--col {
			@include breakpoint(small){
				float: left;
				width: 27%;
				margin-right: 30px;
			}
			@include breakpoint(medium){
				width: 26%;
			}
		}
		&--logo{
			img{
				@include breakpoint(small){
				float: right;
			}
			}
			@include breakpoint(small){
				float: right;
				width: 10%;
				margin-right: 0;
			}
		}
		&:after {
			clear: both;
			content: "";
			display: block;
		}
	}
	.foot-link {
		margin:60px 0;
		@include breakpoint(small){
			margin:0;
		}
		ul{
			margin-bottom: 0;
		}
		li {
			margin-bottom: 13px;

			a {
				display: inline-block;
				color: $white;
				margin-bottom: 15px;
				text-transform: uppercase;
				letter-spacing: .1em;
				font:$bold 13px/1 $primary-font;
				margin: 0;
				&:hover{
					color: $theme-color;
				}

			}
			&:last-child{
				margin-bottom: 0;
			}
		}
		&--post {
			li {
				border-bottom: 1px dashed rgba(255, 255, 255, .1);
				padding: 20px 0;

				&:first-child {
					padding-top: 0;
				}
				&:last-child {
					border: none;
					padding-bottom: 0;
				}
			}
			h4 {
				margin-bottom: 30px;
			}
			ul{
				h4{
					text-transform: none;
					margin-bottom: 0;
				}
			}
			span {
				color: #888;
				display: block;
				font: italic 13px 'Crete Round', serif;
			}
		}
	}
	.foot-subscribe {
			p{
				margin-bottom: 30px;
			}
			@include breakpoint(small){
				float: right;
				width: calc(52% - 90px);
			}
			@include breakpoint(medium){
				width: calc(40% - 90px);
			}

		&:after {
			clear: both;
			content: "";
			display: block;
		}
		h1, h2, h3, h4, h5, h6, p, a {
			color: #FFF;
		}
		.input-group {
			// background: rgba($black, 0.2);
		}
		.input-group-addon {
			background: rgba($black, 0.35);
			border: 1px solid rgba($black, 0.25);
			border-right: none;
			float: left;
			padding: 3px 8px;
			border-radius: 4px 0 0 4px;
			-webkit-border-radius: 4px 0 0 4px;
			-moz-border-radius: 4px 0 0 4px;
		}
		input[type="email"], input[type="text"] {
			background: rgba($black, 0.2);
			border: 1px solid rgba($black, 0.25);
			border-right: none;
			float: left;
			padding: 7px 10px;
			height: 32px;
			color: #999;
			@include breakpoint(small){
				width:58%;
			}

			&:focus {
				background: rgba($black, 0.3);
			}
		}
		button {
			padding: 7px 10px;
			height: 32px;
			border-radius: 0 4px 4px 0;
			-webkit-border-radius: 0 4px 4px 0;
			-moz-border-radius: 0 4px 4px 0;
			border: 1px solid rgba($black, 0.25);
			line-height: 1;
		}
		&__social {
			strong {
				display: block;
			}
			li {
				float: left;
				margin-right: 10px;
				width: calc(50% - 10px);

				&:last-child {
					margin-right: 0;
				}
				a {
					color: #999;

					i {
						width: 40px;
						height: 40px;
						float: left;
						@include border-radius(4px);
						text-align: center;
						line-height: 40px;
						font-size: 20px;
						margin-right: 10px;
						color: #FFF;
					}
					&:hover {
						color: #aaa;
					}
				}
			}
		}
	}
	.contact-info {
		p{
			margin-bottom: 30px;
		}
		&__address {
			h4 {
				// text-transform: none;
				font-size: 14px;
				margin-bottom: 10px;
			}
			p{
				color:rgba($white,.6);
			}
			li {
				&:first-child {
					margin-bottom: 30px;
				}
			}
		}

	}
	.foot-copyright {
		background:$white;
		padding:28px 0;
		color:$dark-gray;
		p{
			color:$dark-gray;
			margin-bottom: 0;
			font:$regular 13px $base-font;
		}
		ul{
			margin-bottom: 0;
			&:after{
				clear:both;
				display: block;
				content:"";
			}
		}
		li{
			@include breakpoint(x-small){
			float: left;
			}
			
			&:first-child{
				margin-left: 0;
			}
			a{
				font:$bold 13px $primary-font;
				color:$dark-gray;
				display:block;
				text-transform: uppercase;
				letter-spacing: .094em;
				&:hover{
					color:$theme-color;

				}
			}
		}
		&__social{
			ul{
				@include breakpoint(small){
					float: right;
				}
				li{
					margin-top: 10px;
					margin-right: 20px;
					@include breakpoint(small){
						margin: 0 0 0 30px;
					}
					a{
						border-bottom: none;
					}
				}
			}

		}
		&__contact{
			clear:both;
			margin-top: 10px;
		}
	}
}
.bg-facebook i {
	background-color: #3B5998;
}
.bg-rss i {
	background-color: #EE802F;
}

.gotoTop {
	display:none ;
	@include breakpoint(small){

    background-color: rgba($base-color, 0.3);
    border-radius: 2px;
    bottom: 50px;
    color: $white;
    cursor: pointer;
    display: none;
    font-size: 20px;
    height: 40px;
    left: auto;
    line-height: 36px;
    position: fixed;
    right: 30px;
    text-align: center;
    top: auto;
    width: 40px;
    z-index: 299;
		
		}
}

footer .main-footer--col.contact-info__address {
	@include breakpoint(small) {
		width: 30%;
		@include border(right, 1px solid #4d4f53);
	}
}