.testimonial {
	background-size: auto;
	padding: 50px;
	h1,h2,h3,h4,h5,h6{
		color:$white;
	}
	.section-title--bordered:after, .section-title--bordered-center:after, .section-title--bordered-right:after{
		background: $white;
	}
	@include breakpoint(small) {
		background-image: url(../../dist/images/swiper/3.jpg);
		padding: 200px 0;
		background-position: 0 0;
		background-size: cover;
		overflow: hidden;
		position: relative;
	}
	&--bg1 {
		background: url(../../dist/images/sun.jpg);
		padding: 50px 0;
		@include breakpoint(small){
			padding: 200px 0;
		}
	}
	h3 {
		color: $white;
	}
	.container {
		transform: translate(0, 0);
		position: static;

		@include breakpoint(small) {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			-webkit-transform: translateY(-50%, -50%);
			-moz-transform: translateY(-50%, -50%);
		}
	}
	.testi {
		background: none;
		border: none;
		text-align: center;
		color: $white;
		max-width: 775px;
		margin: 0 auto;
	}
	&__image {
		width: 72px;
		height: 72px;
		margin: 0 auto 20px;

		img {
			@include border-radius(50%);
			display: block;
			width: 100%;
		}
	}
	&__content {
		font-size: 18px;
		@include breakpoint(small){
			font-size: 26px;
		}

		&__meta {
			font-weight: $bold;
			font-size: 14px;
			margin-top: 15px;

			span {
				font-weight: $regular;
				display: block;
			}
		}
	}
}