.grid-wrapper{
	&:after{
		clear:both;
		display: block;
		content:"";
	}
}
.grid-wrapper {
	@include breakpoint(x-small){
		display: flex;
		flex-wrap:wrap;
		width: calc(100% + 15px);
		margin: 0 0 0 -15px;
	}
	@include breakpoint(small){
		margin: -30px 0 0 -15px;
	}

	&:after {
		clear: both;
		display: block;
		content: "";
	}
	.grid {
		position: relative;
		list-style: none;
		text-align: center;
		margin-bottom: 30px;
		&:last-child{
			margin-bottom: 0;
		}
		@include breakpoint(x-small){
			float: left;
			width: calc(50% - 15px);
			margin: 30px 0 0 15px;
		}
		@include breakpoint(medium){
			width: calc(33.33% - 15px);
		}

		figure {
			// &:hover:before {
			// 	position: absolute;
			// 	left: 0;
			// 	right: 0;
			// 	top: 0;
			// 	bottom: 0;
			// 	background: rgba(0, 0, 0, .5);
			// 	content: "";
			// }
			position: relative;
			overflow: hidden;
			width: 100%;
			max-height: 360px;
			height: auto;
			background: rgba($black,.5);
			text-align: center;
			cursor: pointer;

			h2 {
				word-spacing: -0.15em;
				font-weight: 300;
			}
			h2, p {
				margin: 0;
			}
			img {
				position: relative;
				display: block;
				width: 100%;
				opacity: 0.8;
			}
			figcaption {
				padding: 30px;
				color: #fff;
				text-transform: uppercase;
				font-size: 1.25em;
				-webkit-backface-visibility: hidden;
				backface-visibility: hidden;
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;

				a {
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					z-index: 1000;
					text-indent: 200%;
					white-space: nowrap;
					font-size: 0;
					opacity: 0;
				}
				&:before, &:after {
					pointer-events: none;
				}
			}
		}
	}
	.effect-hover {
		background: rgba($black, .5);

		img {
			max-width: none;
			
			-webkit-transition: opacity 1s, -webkit-transform 1s;
			transition: opacity 1s, transform 1s;
			-webkit-backface-visibility: hidden;
			object-fit:cover;
			backface-visibility: hidden;
			@include breakpoint(medium){
				height: 200px;
			}
		}
		figcaption {
			text-align: left;
		}
		h2 {
			position: relative;
		}
		p {
			display: inline-block;
			color: $white;
			text-transform: none;
			font-size: 15px;
			line-height: 1.5;
			-webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
			transition: opacity 0.35s, transform 0.35s;
			-webkit-transform: translate3d(-360px, 0, 0);
			transform: translate3d(-400px, 0, 0);

			&:first-child {
				-webkit-transition-delay: 0.15s;
				transition-delay: 0.15s;
			}
		}
		&:hover {
			p {
				opacity: 1;
				-webkit-transform: translate3d(0, 0, 0);
				transform: translate3d(0, 0, 0);

				&:first-child {
					-webkit-transition-delay: 0s;
					transition-delay: 0s;
				}
			}
			img {
				opacity: 0.4;
				-webkit-transform: scale3d(1.1, 1.1, 1);
				transform: scale3d(1.1, 1.1, 1);
			}
		}
	}
}
h2.resp-accordion{
	font-weight: $regular;
	font-size: 15px;
}
h2.resp-tab-active a{
	color:$theme-color;
}
.resp-vtabs ul.resp-tabs-list {

    border: 1px solid #c1c1c1;
    border-right: 1px solid #FFF;
    margin-right: -1px;
	margin-top: 0 !important;				
	@include breakpoint(medium){
		width: 25%;
	}
	li{
		margin-bottom: 0;
		background: $light-gray;
		a{
			font-size: 14px;
			display: block;
			padding:14px 15px 15px 14px !important
		}
		&:hover{
			a{
				color:$theme-color;
			}
		}
	}
	li.resp-tab-active{
		border-left: none !important;
		background: $white !important;
		&:first-child{
			border-top:1px solid transparent !important;
		}
		&:last-child{
			border-bottom:1px solid transparent !important;
		}
	}
}
.resp-vtabs .resp-tabs-container {
	@include border-radius(0);
	@include breakpoint(medium){
		width: 75%;
	}
}
.resp-vtabs li.resp-tab-active{
	border:1px solid #c1c1c1 !important;
	border-right:1px solid $white !important;
	margin-bottom: 0 !important;
}
.grid-view h2 {
	font-size: 16px;
	font-weight: $regular;
	margin-bottom: 0;
	margin-top: 10px;
	clear: both;
}

.boxes-packaging.three-col-list.removalist .resp-vtabs 
	{
		ul.resp-tabs-list {
			display: none;
			@include breakpoint(medium){
				display: block;
			}
			li{
				width: 100%;
				float: none;
			}
	}
}