.entry {
	h1, h2, h3, h4, h5, h6 {
		font-size: 17px;
		text-transform: uppercase;
		letter-spacing: 1px;
		line-height: 1.5;

		a {
		}
	}
	ul {
		list-style: none;
		margin-top: 15px;
		li{
			a{
				font:italic 13px/14px 'Crete Round', serif;
				color:#999;
			}
		}
	}
	.more-link{
		&:hover{
			color:$base-color;
			border-bottom-color: $base-color;
		}
	}
}