.promo {
	position: relative;
	// padding: 30px;
	@include breakpoint(medium){
		display: flex;
		align-items: center;
	}
	
	@include border-radius(3px);

	&--light {
		background: #F5F5F5;
	}
	&:after {
		clear: both;
		display: block;
		content: "";
	}
	&__content {
		h1, h2, h3, h4, h5, h6 {
			margin-bottom: 10px;
		}
		margin-bottom: 30px;
		@include breakpoint(medium) {
			float: left;
			width: calc(100% - 250px);
			margin-bottom:0;
		}
	}
	&__cta {
		@include breakpoint(medium) {
			// position: absolute;
			right: 30px;

			a {
				float: right;
			}
		}
	}
}
.promo--bordered.promo {
	@include border(all, 1px solid #E5E5E5);
	.promo__cta{
		position: static;
		width: 100%;
		a{
			float: inherit;
		}
	}
}