.pagination{
	    display: inline-block;
	    padding-left: 0;
	    margin: 20px 0;
	    border-radius: 4px;
	    ul,li{
	    	list-style: none;
	    	margin:0;
	    }
	    li{
	    	float: left;
	    	margin:0;
	    	&:first-child{
	    		a,span{
	    			margin-left: 0;
				    border-bottom-left-radius: 4px;
				    border-top-left-radius: 4px;
	    		}
	    	}
	    	a{
	    		color:$theme-color;
	    		position: relative;
			    float: left;
			    padding: 6px 12px;
			    line-height: 1.42857143;
			    text-decoration: none;
			    background-color: $white;
			    border: 1px solid #dddddd;
			    margin-left: -1px;
			    &:hover{
			    	background-color: $light-gray-1;
    				border-color: #dddddd;
			    }
	    	}
	    	.current{
	    		border:1px solid $theme-color;
	    		background: $theme-color;
	    		cursor: default;
	    		color:#FFF;
	    		padding: 6px 12px;
			    line-height: 1.42857143;
			    float: left;

	    	}
	    	&:last-child{
	    		a,span{
	    			border-bottom-right-radius: 4px;
   					 border-top-right-radius: 4px;
	    		}
	    	}
	    }
}