.search {
	position: absolute;
	top: 10px;
	right: 20px;

	@include breakpoint(small) {
		float: right;
		margin: 42px 0 32px 15px;
		position: static;
	}
}
.top-cart {
	@extend .search;
	display: none;

	@include breakpoint(small) {
		display: block;
	}
}

.sticky .menu-mobile{
	top:4px;
}
.menu-mobile {
	position: absolute;
	top: 6px;
	right: 20px;

	@include breakpoint(x-small) {
		float: left;
		// margin-top: 40px;
	}
	// @include breakpoint(small) {
	// 	top: 20px;
	// }
	@include breakpoint(medium) {
		display: none;
	}
	&:after {
		content: "\f0c9";
		font-family: "FontAwesome";
		font-size: 24px;
		padding: 0;
		float: left;
		position: relative;
		color:$theme-color;
	}
}
.menu-dropdown-icon {
	position: relative;
	@include transition(0.5s);

	&:before {
		content: "\f107";
		font-family: $icon-font;
		cursor: pointer;
		float: right;
		color: $base-color;
		position: absolute;
		font-size: 12px;
		right: 0;
		line-height: 1;
		top:15px;

		@include breakpoint(medium) {
			top: 28px;
			right: -15px;
		}
		.sticky & {
			top: 14px;
			@include transition(0.5s);
		}
	}
}
.menu-dropleft-icon {
	position: relative;
	&:before {
		content: "\f105";
		font-family: "FontAwesome";
		cursor: pointer;
		float: right;
		padding: 5px 0;
		color: $base-color;
		position: absolute;
		right: 15px;
		top: 5px;
		display: none;

		@include breakpoint(small) {
			display: block;
		}
	}
}


.navigation {
	width: 100%;
	@include transition(0.5s);

	@include breakpoint(medium) {
		float: right;
		width: auto;

	}
}



.primary {
	border-right: none;
	margin-right: 0;
	padding-right: 0;
	@include transition(0.5s);

	@include breakpoint(medium) {
		float: right;
		border: none;
	}

	.menu {
		display: none;
		margin:0;
		@include breakpoint(medium) {
			margin: 0 auto;
			width: 100%;
			list-style: none;
			padding: 0;
			box-sizing: border-box;
			display: block;
		}
		&:after{
			clear:both;
			display: block;
			content:"";
		}
		&.show-on-mobile {
			display: block;
		}
		li {
			@include border(bottom,1px solid rgba($black,.2));
			display: block;
			padding: 0;
			

			@include breakpoint(medium) {
				float: left;
				border: none;
				margin: 0 0 0 20px;
			}
			@include breakpoint(large){
				margin: 0 0 0 30px;
			}
			&:last-child {
				@include border(bottom,1px solid rgba($black,.2));

				@include breakpoint(small) {
					border-bottom: none;
				}
				a{
					width:100%;
					background: $theme-color;
					color:$white;
					text-align: center;
					&:hover{
						background: $theme-color-alt;
					}
				}
				@include breakpoint(xx-small){
					display: none;
				}
			}
			&:hover {
				color: $theme-color;

				&.menu-dropdown-icon {
					&:before {
						color: $theme-color;
					}
				}
				@include breakpoint(small) {
					// background: $light-gray;
				}
			}
			a {
				padding: 15px 0;
				line-height: 30px;
				width: 85%;
				display: block;
				font: 400 12px/1 $primary-font;
				@include transition(0.5s);

				@include breakpoint(medium) {
					text-decoration: none;
					padding: 0;
					line-height: 68px;
					width: 100%;
				}
			}
			ul{
				position: relative;
				border-top: none;
				left: 0;
				padding: 0;
				width: 100%;
				margin-bottom: 0;
				display: none;

				@include breakpoint(medium) {
					display: none;
					//width: 95%;
					width: 200px;
					background: $white;
					border-top: 2px solid $theme-color;
					//padding: 20px;
					position: absolute;
					z-index: 99;
					//left: 30px;
					left: auto;
					margin: 0;
					list-style: none;
					box-sizing: border-box;
				}
				
				li {
					@include border(top,1px solid rgba($black, .2));
					@include border(bottom,none);


					@include breakpoint(medium) {
						//width: 105%;
						margin: 0;
						padding-bottom: 0;
						list-style: none;
						width: 100%;
						background: none;
						float: left;
						position: relative;
						border:none;
					}
					&:last-child {
						border-bottom: none;
						border-right: none;
						padding-left: 0;
						display: block;
						a{
							width:auto;
							background: none;
							color:#3b3d40;
							text-align: left;
							&:hover{
								background:none;
								color:$theme-color;
							}
						}
					}
					&:hover {
						@include breakpoint(small) {
							background: $light-gray;
						}
					}
					a {
						@include transition(0.3s);
						padding:15px 20px;

						@include breakpoint(small) {
							padding: 10px 0;
							display: block;
							font-size: 12px;
							line-height: normal;
							padding: 16px 15px;
							margin:0;
						}
						&:hover {
							margin-left: 10px;
							background: none;
							color: $theme-color;
							ul{
								display: block;
							}
						}
					}
					ul {
						position: relative;
						border-top: none;
						left: 0;
						padding: 0;
						width: 100%;
						margin-left: 0;
						margin-bottom: 0;
						display: block;

						@include breakpoint(small) {
							display: none;
							position: absolute;
							top: 0;
							margin-left: 200px;
							background: $white;
							border-top: 2px solid $theme-color;
							width: 200px;
						}
						li {
							width: 100%;
							border-top: 1px solid $gray;

							&:hover {
								background: $light-gray;
							}
							@include breakpoint(small) {
								border-top: none;
							}
							a {
								padding-left: 30px !important;
								@include transition(0.3s);

								@include breakpoint(small) {
								}
							}
						}
					}
				}
			}
		}
	}
}
.primary .menu li ul li:hover ul{
	display: block;
}
.current_page_parent a{
	color:$theme-color;
}
.current_page_parent .sub-menu a{
	color:$gray;
}
.current_page_item a, .current_page_parent .current_page_item a{
	color:$theme-color;
}