.social-icons {
	li {
		display: inline-block;
		padding-right: 10px;

		&:last-child {
			padding-right: 0;
		}
		a {
			width: 32px;
			height: 32px;
			font-size: 18px;
			display: block;
			overflow: hidden;

			.icon {
				display: block;
				position: relative;
				text-align: center;
				height: 32px;
				padding-top: 9px;
				@include transition(0.3s);
				background: $gray;
				color: $base-color;

				&:last-child {
					color: $white;
				}
				&--rounded {
					@include border-radius(3px);
				}
				&--circle {
					@include border-radius(50%);
				}
				&--light {
				}
				&--nobg {
					background: none;
				}
				&--bg {
					color: $white;
				}
			}
			&:hover {
			
					.fa-facebook {
						background: #3b5998;
					}
					.fa-twitter {
						background: #00acee;
					}
					.fa-google-plus {
						background: #dd4b39;
					}
					.fa-pinterest {
						background: #c8232c;
					}
					.fa-vimeo {
						background: #86c9ef;
					}
					.fa-git {
						background: #171515;
					}
					.fa-yahoo {
						background: #720e9e;
					}
					.fa-linkedin {
						background: #0e76a8;
					}
					.fa-rss {
						background: #ee802f;
					}
				}
			}
			&--small {
				a {
					width: 32px;
					height: 32px;
					font-size: 18px;

					.icon {
						height: 32px;
						padding-top: 9px;
					}
				}
			}
			&--medium {
				a {
					width: 40px;
					height: 40px;
					font-size: 20px;

					.icon {
						height: 40px;
						padding-top: 12px;
					}
					&:hover {
						.icon {
							&:first-child {
								&:after {
									margin-top: -40px;
								}
							}
						}
					}
				}
			}
		}
	}
	.subscribe {
		li {
			float: left;
			margin-top: 30px;

			a {
				float: left;

				&.small {
					display: block;
					width: auto;
					height: auto;
					margin-left: 10px;
					font-size: 16px;
				}
			}
			&:last-child {
				margin-left: 15px;

				@media (min-width:768px) and (max-width:988px) {
					margin-left: 0;
				}
			}
		}
	}

.one-col, .two-col, .three-col,.four-col, .full-width{
	.social-icons{
		margin-top: 15px;
	}
}