//Accordian
.accordion, .accordion * {
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
}
.accordion {
	overflow: hidden;

	&__section {
		&__title {
			width: 100%;
			padding: 15px 15px 15px 30px;
			display: inline-block;
			border-bottom: 1px solid rgba($black, .1);
			transition: all linear 0.15s;
			font-size: 15px;
			font-weight: bold;
			color: $dark-gray;
			position: relative;

			i {
				@include rotate(-90deg);
				position: absolute;
				top: 20px;
				left: 5px;
			}
			&.active {
				background: rgba($black, .1);
				text-decoration: none;
				color: $dark-gray;

				i {
					@include rotate(360deg);
					top: 15px;
					left: 10px;
				}
			}
			&:hover {
				background: rgba($black, .1);
				text-decoration: none;
				color: $dark-gray;
			}
		}
		&__title {
		&:last-child {
			
				border-bottom: none;
			}
		}
		&__content {
			padding: 15px;
			display: none;
		}
	}
}