.not-found{
	h2{
		font-size: 100px;
	color: $theme-color;
	line-height: 1;
	margin-bottom: 0;
	@include breakpoint(small){
		font-size: 200px;
	}
	}
	h3{
		font-size: 20px;
		color:$dark-gray;
		@include breakpoint(small){
		font-size: 30px;
	}
	}
	p{
		font-size: 16px;
		margin-bottom: 30px;
		@include breakpoint(small){
		font-size: 20px;

		margin-bottom: 60px;
	}
	}
}

.error404 header, .error404 footer,
.error404 .top-nav {
    display: none;
}

.error404-html, .error404, .not-found, .error404 .container {
    height: 100%;
}

.not-found {
    display: table;
    width: 100%;
}

.error404 .container {
    display: table-cell;
    vertical-align: middle;
}