//Swiper Slider
@import "../base/typography";

.owl-prev {
	&.disabled {
		display: none;
	}
}
.owl-next {
	&.disabled {
		display: none;
	}
}
.navcenter {
	position: absolute;
	top: 50%;
	width: 100%;
	transform: translateY(-50%);
	-moz-transform: translateY(-50%);
	-webkit-transform: translateY(-50%);
}
.slider {
	position: relative;
	margin: 0;

	.slide{
		position: relative;
		&:after{
			content:"";
			position: absolute;
			left: 0;
			right: 0;
			top:0;
			bottom: 0;
			background: rgba(52,56,75,.3);
		}
		.container{
				z-index: 1;
				height: 100%;
				display: table;
			}
	}
	.main-slider {
		text-align: center;
		font-size: 18px;
		//background: $black;
		position: relative;
		// height: 180px;

		@include breakpoint(small) {
			height: 690px;
		}
		.owl-item {
			.slide {
				padding:50px 0;
				height: 600px;

				@include breakpoint(small) {
					height: 690px;
					padding: 0;
				}
				.container{
					@include breakpoint(small){
						max-width: 700px;
					}
				}
			}
		}
		&--single {
			height: 300px;

			@include breakpoint(small) {
				height: 689px;
			}
		}
	}
	&__caption {
		// max-width: 800px;
		padding: 0 15px;
		position: relative;
		z-index: 1;
		height: 100%;
		vertical-align: middle;
		display: table-cell;
		// margin-left: -150px;
		// margin-top: -30px;
		// top: 50%;
		// left: 50%;
		// transform: translate(-50%, -50%);
		// -webkit-transform: translate(-50%, -50%);
		// -moz-transform: translate(-50%, -50%);

		@include breakpoint(small) {
			// margin-left: -400px;
			// margin-top: -176px;
		}
		h2 {
			font: $bold 28px $primary-font;
			color:$white;

			@include breakpoint(small) {
				font-size: 52px;
				margin-bottom: 15px;
			}
		}
		p {
			font:400 16px/27px $base-font;
			margin-bottom: 10px;
			color: $white;
			// display: none;


			@include breakpoint(small) {
				display: block;
				margin-bottom: 140px;
			}
		}
		.button {
			// display: none;

			@include breakpoint(small) {
				display: inline-block;
				padding: 0 42px;
			}
		}
	}
	&__arrow {
		bottom: 20px;
		color: #fff;
		font-size: 32px;
		height: 32px;
		left: 50%;
		margin-left: -16px;
		position: absolute;
		text-align: center;
		width: 32px;
		z-index: 99999;
		cursor: pointer;
		display: none;

		@include breakpoint(small) {
			display: block;
		}
	}
}
.owl-carousel .owl-stage-outer {
	width: 100%;
}
//carousel thumbnails slider navigation styling

.thumbnails {
	position: relative;

	.owl-nav {
		@extend .navcenter;

		.owl-prev {
			position: absolute;
			background: #fff;
			@include border-radius(50%);
			width: 36px;
			height: 36px;
			line-height: 36px;
			text-align: center;
			font-size: 18px;
			top: 50%;
			margin-top: -18px;
			right: 0;
			@include transition();
			opacity: 0;

			.related-products & {
				margin-top: -70px;
			}
			@include breakpoint(small) {
				right: -36px;
			}
			i {
				color: $base-color;
				text-align: center;
			}
		}
		.owl-next {
			@extend .owl-prev;
			left: 0;
			right: auto;

			@include breakpoint(small) {
				left: -36px;
			}
		}
	}
	&:hover {
		cursor: pointer;

		.owl-prev {
			opacity: 1;
			right: 0;
			background: $theme-color;

			@include breakpoint(small) {
				right: -18px;
			}
			i {
				color: $white;
			}
		}
		.owl-next {
			@extend .owl-prev;
			left: 0;
			right: auto;

			@include breakpoint(small) {
				left: -18px;
			}
		}
	}
}
//carousel main slider navigation styling
.main-slider, .fadeOut, .carousel-thumbs {
	.owl-nav {
		@extend .navcenter;

		.owl-prev {
			position: absolute;
			background: rgba($black, 0.5);
			@include border-left-radius(3px);
			width: 52px;
			height: 52px;
			line-height: 52px;
			text-align: center;
			font-size: 34px;
			top: 50%;
			margin-top: -26px;
			right: 0;
			@include transition(0.2s);
			opacity: 1;

			&:hover {
				background: rgba($black, 0.7);
			}
			i {
				color: $white;
				text-align: center;
			}
		}
		.owl-next {
			@extend .owl-prev;
			@include border-right-radius(3px);
			left: 0;
		}
	}
	&:hover {
		cursor: pointer;
	}
}
//carousel thumbs slider dots styling

.carousel-thumbs {
	.owl-item {
		height: auto;
		margin-bottom: 30px;

		@include breakpoint(medium) {
			height: 500px;
		}
	}
	.owl-dots {
		.owl-dot {
			float: left;
			background-size: cover;
			margin-top: 10px;
			opacity: 0.7;

			&.active {
				opacity: 1;
			}
			span {
				opacity: 0;
			}
		}
	}
}