.removalist, .three-col-list.removalist{
	.section-title{
		&:after{
			clear: both;
		    display: block;
		    content: "";
		}
		.section-title__content{
			@include breakpoint(small){
				float:left;
			}
		}
		
		.global-form{
			@include breakpoint(small){
				float:right;
				width: 300px;
			}
			ul{
				
				@include breakpoint(small){
				margin: -15px 0 0 -30px;
				}
			li{
				width: 100%;
			}
			}
		}
	}
	.pagination{
		display: block;
		margin:50px 0 0 0;
		ul{
			text-align: center;
			display: block;
			margin:0;
			li{
				margin:0;
				width: auto;
				display: inline-block;
				float:none;
				padding: 0;
				
				a{
					@include border(all, 1px solid rgba($black, .05));
					padding: 0;
					width: 48px;
				height: 48px;
				text-align: center;
				line-height: 48px;
				}
				.current{
					@include border(all, 1px solid rgba($black, .05));
					width: 48px;
				height: 48px;
				text-align: center;
				line-height: 48px;
				padding: 0;
				}
			}
		}
	}
}
