.generic-format {
	font-size: 14px;
	line-height: 20px;
	 table {
	    width: 100%;
	    border: none;
	    outline: none;
	    border-collapse: collapse;
	    font-size: 16px;
	    td {
		    text-align: left;
		    padding: 15px;
		}
		th {
			padding: 15px;
		    background: $theme-color;
		    color:$white;
		    text-align: left;
		}
		tr:nth-child(odd) {
		    background: rgba(0,0,0,.1);
		}
	}
 
	p {
		margin-bottom: 15px;
		font-size: 16px;
		line-height: 1.5;

		&:last-child {
			margin-bottom: 0;
		}
	}
	hr{
		margin-bottom: 15px;
		
	}
	ul, ol {
		padding-left: 16px;

		li {
			margin-bottom: 15px;
			padding-left: 10px;

			&:last-child {
				margin-bottom: 0;
			}
		}
	}
	a{
		display: inline-block;
		vertical-align: middle;
		color:$theme-color;
		&:hover{
			color:$theme-color-alt;
		}
	}
	img{
		display: inline-block;
		vertical-align: middle;
		margin:0 !important;
	}
	pre{
		overflow: scroll;
	}
	blockquote{
		padding-left: 30px;
		@include border(left,6px solid rgba($black,.2));
	}

}

.video-container, .wp-video {
	position:relative;
	padding-bottom:56.25%;
	padding-top:30px;
	height:0;
	overflow:hidden;
	width: 100% !important;
	iframe,object,embed,video{
		position:absolute;
	top:0;
	left:0;
	width:100%;
	height:100%;
	}
}
.wp-video{
	margin-bottom: 30px;
}

.no-banner{
	display: none;
}
.hero-banner{
	position: relative;
	padding:75px 0;
	height: 450px;
	background-repeat: no-repeat;
	background-size: cover;
	@include breakpoint(small){
		padding:150px 0;
		height: 590px;
	}
	@include breakpoint(large){
		padding:200px 0;
	}
	&:before{
		position: absolute;
		left: 0;
		right: 0;
		top:0;
		bottom:0;
		background: rgba($black,.5);
		content:"";
	}
	.container{
		z-index: 99;
		max-width: 700px;
	}
	h1,h2,h3,h4,h5,h6,p{
		color:$white;
	}
	h1,h2,h3,h4,h5,h6{
		font:$bold 28px $primary-font;
		@include breakpoint(small){
			font:$bold 52px $primary-font;
		}
	}
	p{
		font:$regular 16px/27px $base-font;
	}
}