.container{
	&:after{
		clear: both;
		display: block;
		content: "";
	}
	margin:0 auto;
	padding: 0 15px;
	position: relative;
	@include breakpoint(small){
		width:750px;
	}
	@include breakpoint(medium){
		width: 970px;
	}
	@include breakpoint(large){
		width: 1030px;
	}
}
