.two-col,.three-col, .four-col{
	h1,h2,h3,h4,h5,h6{
		margin-bottom: 0;
	}
	@include breakpoint(small){
		margin-left: -60px;
		display: flex;
		flex-wrap: wrap;
	}
	@include breakpoint(small){
		.col{
			margin-left: 60px;
			float: left;
		}
	}

	.col{
		margin-bottom: 30px;
		@include breakpoint(small){
			margin-bottom:0;
		}
	}
	&:last-child{
		margin-bottom: 0;
	}
	&:after{
		clear:both;
		display: block;
		content:"";
	}
}
.two-col{
	.col{
		@include breakpoint(small){
			width: calc(50% - 30px);
		}
	}
}
.three-col{
	.col{
		@include breakpoint(small){
			width: calc(33.33% - 60px);
		}
	}
}

.four-col{
	@include breakpoint(small){
		display: block;
	}
	@include breakpoint(medium){
		display: flex;
	}
	.col{
		@include breakpoint(small){
			width: calc(50% - 30px);
		}
		@include breakpoint(medium){
			width: calc(25% - 30px);
		}
	}
}

.full-width{
	&__one-third{
		@include breakpoint(small){
			float: left;
			width: 38%;
			margin-right: 4%;
		}
	}
	&__two-third{
		@include breakpoint(small){
			float: right;
			width: calc(62% - 4%);
		}
	}
	&--alt{
		&__one-third{
			background: red;
		}
	}
	&:after{
		clear: both;
		display: block;
		content:"";
	}
}

.full-width--alt .full-width__one-third{
		@include breakpoint(small){
			float: right;
			width: 38%;
			margin-left:4%;
			margin-right: 0;
	}
.full-width--alt .full-width__two-third{
		@include breakpoint(small){
			float: left;
			width: calc(62% - 4%);
		}
	}
}