.contact-form {
	.half-width {
		@include breakpoint(small) {
			width: 201px;
		}
		@include breakpoint(medium) {
			width: 265px;
		}
		@include breakpoint(large) {
			width: 282px;
		}
	}
	.section-title__content {
		max-width: 100%;
	}
}
.full-width--alt {
	.full-width__two-third {
		@include breakpoint(small) {
			float: left;
		}
	}
}
.contact-form{
	li{
		&:nth-child(2n) {
			margin-right: 0;
		}
	}
}
.one-col.contact-form {
	max-width: 100%;
	.container{
		width: 750px;
	}
	label {
		display: block;
		font-weight: $bold;
		margin-bottom: 5px;
	}
	.name_first label {
		display: none;
	}
	.gfield_required {
		color: red;
		margin-left: 3px;
	}
	.container {
		@include breakpoint(small) {

		}
	}
	li {
		@include breakpoint(small) {
			width: 352px;
		}
		&:nth-child(2n) {
			margin-right: 0;
		}
		// &:last-child{
		// 	width: 100%;
		// 	clear: both;
		// 	padding-top: 15px;
		// }
	}
}
.cta{
	.button--green{
		&:hover{
			background: $dark-gray;
		}
	}
}

.full-width__one-third{
	margin-top: 60px;
	@include breakpoint(small){
		margin-top: 0;
	}
}

.sidebar-widget{
	p,a{
		font-size: 14px;
		color:#3b3d40;
	}
	a:hover{
		color:$theme-color;
	}
}