.feature, .feature-numbered{
	h1,h2,h3,h4,h5,h6{
		font-weight:700;
		font-size: 16px;
		margin:30px 0 10px 0;
	}
}
.feature-numbered{
	h1,h2,h3,h4,h5,h6{
		margin-top: 0;
	}
	.col{
		display: flex;
		flex-wrap:nowrap;
	}
	&__decimal{
		min-width: 75px;
		font-size: 48px;
		color:$theme-color;
		line-height: 1;
	}
}

.feature-box{
	h1,h2,h3,h4,h5,h6{
		font-size: 16px;
		text-transform: uppercase;
		margin:30px 0 10px;
	}
}