//Sticky header
.stickyheight {
	height: auto;
	@include transition(0.5s);

	@include breakpoint(small) {
		@include transition(0s);
	}
}
.sticky {
	.header {
		&__wrap {
			position: relative;
			background: $white;
			box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
			padding: 15px 0;

			@include breakpoint(small) {
				position: fixed;
				top: 0;
				left: 0;
				width: 100%;
				z-index: 999;
			}
		}
		.logo {
			img {
				height: 40px;

				@include breakpoint(small) {
					height: 40px;
				}
			}
		}
		.navigation {
			@extend .stickyheight;

			.primary {
				@extend .stickyheight;

				// .menu {
				// 	li {
				// 		a {
				// 			@extend .stickyheight;

				// 			@include breakpoint(small) {
				// 				margin:0 0 0 15px;
				// 			}
				// 		}
				// 		li {
				// 			a {
				// 			}
				// 		}
				// 	}
				// }
			}
		}
		#top-search {
			margin-top: -45px;

			@include breakpoint(small) {
				margin: 22px 0 20px 15px;
			}
		}
		.top-cart {
			margin: 20px 0 18px 15px;

			&__content {
				top: 60px;
			}
		}
	}
}
body.top-search-open.sticky #top-search form {
	height: 60px;
}
.sticky .primary .menu li a{
	@include breakpoint(medium){
		line-height: 40px;
	}
}
.sticky .primary .menu li li a{
	@include breakpoint(medium){
		line-height: 1;
	}
}