.button {
	border: medium none;
	@include border-radius(22px);
	color: $white;
	cursor: pointer;
	display: inline-block;
	font: $bold 10px/45px $primary-font;
	height: 45px;
	letter-spacing: .2em;
	// margin: 5px;
	outline: medium none;
	padding: 0 17px;
	position: relative;
	text-transform: uppercase;
	white-space: nowrap;
	min-width: 148px;

	&:hover {
		@include transition(0.3s);
		color: $white;
		background: rgba($base-color, 0.3);
	}
	&--green {
		background: $theme-color;
		&:hover{
			background: $theme-color-alt;
		}
	}
	&--3d {
		border-bottom: 3px solid rgba(0, 0, 0, 0.15);

		&:hover {
			opacity: 0.9;
		}
	}
	&--small {
		font-size: 12px;
		height: 34px;
		line-height: 34px;
		padding: 0 17px;
	}
	&--medium {
		font-size: 16px;
		height: 46px;
		line-height: 46px;
		padding: 0 26px;

		&:hover {
			background: $dark-gray;
		}
	}
	&--xlarge {
		font-size: 18px;
		height: 52px;
		letter-spacing: 2px;
		line-height: 52px;
		padding: 0 32px;

		&:hover {
			background: $dark-gray;
		}
	}
	&--border {
		@include border(all, 1px solid $gray);
		color: $gray;
		text-shadow: none;

		&:hover {
			background: $theme-color;
			@include border(all, 1px solid $theme-color);
			color: $white;
		}
		&--green {
			border: 2px solid $theme-color;
			color: $theme-color;

			&:hover {
				background: $theme-color;
				color: $white;
			}
		}
		&--light {
			border: 2px solid $white;

			&:hover {
				background: rgba($black, 0.25);
				border-color: transparent;
			}
		}
		&--greenhover {
			border: 2px solid #DDD;
			color: $dark-gray;
			text-shadow: none;

			&:hover {
				background: $theme-color;
				border-color: transparent;
			}
		}
		&--purplehover {
			border: 2px solid $dark-gray;
			color: $gray;
			text-shadow: none;

			&:hover {
				background: #9a5cb2;
				border-color: transparent;
			}
		}
	}
}

.btn{
	display:inline-block;
	font-weight: normal;
	text-align: center;
	vertical-align: middle;
	touch-action:manipulation;
	cursor: pointer;
	border:1px solid transparent;
	line-height: 1.428587;
	font-size: 14px;
	@include border-radius(4px);
}
.btn-success{
	color: #fff;
    background-color: #5cb85c;
    border-color: #4cae4c;
    &:hover{
    	    background-color: #449d44;
    border-color: #398439;
    }
}