.half-width-section{
	padding:0 15px;
	margin-bottom: 30px;
	@include breakpoint(small){
		padding:0;
		display: flex;
		margin-bottom: 0;
	}
	&__image{
		margin-bottom: 30px;
		img{
			display: block;
			@include breakpoint(small){
				display: none;
			}
		}
		@include breakpoint(small){
			min-height: 475px;
			margin-bottom: 0;
		}
	}
	&__content, &__image{
		@include breakpoint(small){
			padding:60px;
			width: 50%;
		}
	}
}

.half-width-section--alt .half-width-section__image{
	@include breakpoint(small){
		order:2;
	}
}
.half-width-section--alt .half-width-section__content{
	@include breakpoint(small){
		order:1;
	}
}