.sidebar-widget{
	margin-bottom: 30px;
    padding: 0 20px;
    // border: 1px solid #eee;
    h2{
    	padding: 10px 20px;
	    font-size: 14px;
	    background: #f5f7f7;
	    border: none;
	    margin-bottom: 10px;
	    margin-right: -20px;
	    margin-left: -20px;
	    text-transform: uppercase;
	    position: relative;
	    &:after{
	    	background-color: $theme-color;
	    	position: absolute;
		    top: 0;
		    left: 0;
		    content: "";
		    height: 100%;
		    width: 3px;
	    }
    }
    ul{
    	margin-bottom: 0;
    	li{
			padding: 10px 0;
			border-bottom: 1px solid #f1f1f1;
			color: #272727;
			font-size: 13px;
			font-weight: 400;
			line-height: 22px;
			list-style: none;
			a{
				display: block;
				columns: #444;
			}
			&:last-child{
	    		border-bottom: none;
	    	}
    	}

    }
    p{
    	line-height: 24px;
    }
}