.breadcrumb{
	background: #e0e0e0;
	padding: 30px 0;
	@include breakpoint(small){
		padding:45px 0;
	}
	span{
		font-size: 28px;
		letter-spacing: 1px;
		font-weight: 600;
		text-transform: uppercase;
		color:$base-color;
		line-height: 28px;
	}
	ul{
		list-style:none;
		margin:6px 0;
		@include breakpoint(small){
			float:right;
		}
		li{
			float: left;
			font-size: 12px;
			color:#777;
			a{
				font-size: 12px;
				color:#555;
			}
			&:before{
				content:"/";
				color: $gray;
				padding:0 5px;
			}
			&:first-child{
				&:before{
					content:"";
				}
			}
		}
	}
}