.bg-color-block{
	h1,h2,h3,h4,h5,h6,p{
		color:#FFF;
	}
	&.two-col, &.three-col, &.four-col{
		margin:0;
		.col{
			margin:0;
			padding:30px;
			@include breakpoint(medium){
				padding: 60px;
			}
			&:nth-child(1){
				background: rgb(155, 214, 113);
			}
			&:nth-child(2){
				background: rgb(154, 92, 178);
			}
			&:nth-child(3){
				background: rgb(114, 114, 114);
			}
			&:nth-child(4){
				background: rgb(155, 214, 113);
			}
		}
	}
	&.two-col{
		.col{
			@include breakpoint(small){
				width: 50%;
			}
		}
	}
	&.three-col{
		.col{
			@include breakpoint(small){
				width: 33.33%;
			}
		}
	}
	&.four-col{
		.col{
			@include breakpoint(small){
				width: 50%;
			}
			@include breakpoint(medium){
				width: 25%;
			}
		}
	}
}